import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  background: #f2f7fa;
  height: 500px;
`;

const Content = styled.div`
  max-width: 980px;
  padding: 0 15px;
  margin: 0 auto;
`;

class MoreRelation extends Component {
  render() {
    return (
      <Div>
        <Content>
          <h2 className="more-title">更多相關的文章</h2>
        </Content>
      </Div>
    );
  }
}

export default MoreRelation;
